import React, {useContext, useState, useEffect, useRef} from 'react'
import {
  Space,
  Table,
  Form,
  Input,
  message,
  Button,
  Tooltip,
  Modal,
  Select,
  Divider,
  Tag,
  AutoComplete,
  Row,
  Col,
} from 'antd'
import type {FormInstance} from 'antd/es/form'
import {mainConfig} from '../../../config/mainConfig'
import Helper from '../../utils/Helper'
import moment from 'moment'
import {PlusOutlined, EyeOutlined} from '@ant-design/icons'

const {Option} = AutoComplete


const EditableContext = React.createContext<FormInstance<any> | null>(null)

let token = '',
  usrRole = ''
interface EditableRowProps {
  index: number
}

const EditableRow: React.FC<EditableRowProps> = ({index, ...props}) => {
  const [form] = Form.useForm()
  return (
    <Form form={form} component={false}>
      <EditableContext.Provider value={form}>
        <tr {...props} />
      </EditableContext.Provider>
    </Form>
  )
}
interface Item {
  key: string
  name: string
  age: string
  address: string
}
interface EditableCellProps {
  title: React.ReactNode
  editable: boolean
  children: React.ReactNode
  dataIndex: keyof Item
  record: Item
  handleSave: (record: Item) => void
}

const EditableCell: React.FC<EditableCellProps> = ({
  title,
  editable,
  children,
  dataIndex,
  record,
  handleSave,
  ...restProps
}) => {
  const [editing, setEditing] = useState<any>(false)
  const inputRef = useRef<any>(null)

  const form = useContext<any>(EditableContext)
  useEffect(() => {
    if (editing) {
      inputRef.current.focus()
    }
  }, [editing])

  const toggleEdit = () => {
    setEditing(!editing)
    form.setFieldsValue({
      [dataIndex]: record[dataIndex],
    })
  }

  const save = async () => {
    try {
      const values = await form.validateFields()
      toggleEdit()
      handleSave({...record, ...values})
    } catch (errInfo) {
      console.log('Save failed:', errInfo)
    }
  }

  let childNode = children

  if (editable) {
    childNode = editing ? (
      <Form.Item
        style={{
          margin: 0,
        }}
        name={dataIndex}
        rules={[
          {
            required: false,
            // message: `${title} is required.`,
          },
          // ({getFieldValue}) => ({
          //   validator(rule, value) {
          //     if (value <= 0) {
          //       return Promise.reject('Price should be greater than 0') // The validator should always return a promise on both success and error
          //     } else {
          //       return Promise.resolve()
          //     }
          //   },
          // }),
        ]}
      >
        <Input ref={inputRef} onPressEnter={save} onBlur={save} />
      </Form.Item>
    ) : (
      <div
        className='editable-cell-value-wrap'
        style={{
          paddingRight: 24,
        }}
        onClick={toggleEdit}
      >
        {children}
      </div>
    )
  }

  return <td {...restProps}>{childNode}</td>
}
token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')
usrRole = JSON.parse(localStorage.getItem('portalUserDataRole') || '')
const ExpandedRowRender = (props: any) => {
  const [dataSet, setDataSet] = useState<any>([])
  const [loader, setloader] = useState<boolean>(false)
  const [openModal, setopenModal] = useState<any>(false)
  const [tag, setTag] = useState<any[]>([])
  const [tagSeverity, setTagSeverity] = useState<any[]>([])
  const [form] = Form.useForm()
  const [productData, setProductData] = useState<any>('')
  const inputRef2 = useRef<any>(null)
  const [name, setName] = useState<any>('')
  const [options, setOptions] = useState<any>([])
  const inputRef5 = useRef<any>(null)
  const [value1, setValue1] = useState<any>({key: '', value: '', children: ''})

  useEffect(() => {
    getSupplierData()
  }, [props, productData])

  const handleSave = (row: any) => {
    
    setloader(true)
    if ((row.oldPrice === row.price || row.oldPrice == row.price) && (row.oldComments === row.comments || row.oldComments == row.comments) && (row.oldForecast === row.forecast || row.oldForecast == row.forecast)) {
      // message.error('')
      setloader(false)
      return
    }
    let payLoad;
    if(row.oldPrice != row.price )
    {
      payLoad = {
        pricing_id: row.pcid,
        negotiated_price: row.price,
      }
   
    }
    else if(row.oldForecast != row.forecast )
    {
      payLoad = {
        pricing_id: row.pcid,
        forecast: row.forecast,
      }
   
    }
    else{  
      payLoad = {
        pricing_id: row.pcid,
        scomment: row.comments,
      }
    }
   
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/update-supplier-pricing`, {
      method: 'post',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(payLoad),
    })
      .then(function (data) {
        data.json().then(function (data) {
          message.success(data.data)
          getSupplierData()
          setloader(false)
        })
      })
      .catch(function (error) {})
  }
  const getPanelValue = (searchText: any) => {
    if (!searchText) {
      setOptions([])
      return
    }
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/search-comment/${searchText}`, {
      method: 'get',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then(function (data) {
        data.json().then(function (data) {
          setOptions(data.comments)
        })
      })
      .catch(function (error) {})
  }

  const getSupplierData = () => {
    setloader(true)
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-supplier-pricing/${props.record['ProductAC4']}`, {
      method: 'get',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then(function (response) {
        if (response.status != 200) {
          setloader(false)
        }
        response.json().then(function (data) {
          if (data.success == true) {
            setDataSet(data.data)
            setloader(false)
          }
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
        setloader(false)
      })
  }
  const handleCancel = () => {
    setopenModal(false)
  }

  const onFinish = (data: any) => {
    setloader(true)
    var tag_id = '',
      custom_tag = ''
    if (data.tag && !isNaN(data.tag)) {
      tag_id = data.tag
    } else {
      custom_tag = data.tag
    }

    const tagObj = {
      tag_id: `${tag_id}`,
      severity: `${data.severity}`,
      pcid: `${productData.pcid}`,
      custom_tag: `${custom_tag}`,
    }

    fetch(`${mainConfig.MAIN_ENDPOINT}/api/assign-tag`, {
      method: 'post',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(tagObj),
    })
      .then(function (data) {
        data.json().then(function (data) {
          if (data.success == true) {
            message.success(data.data)
            getSupplierData()
          }
        })
        if (data.status == 204) {
        }
      })
      .catch(function (error) {})
    setopenModal(false)
    setloader(false)
  }
  const onCommentFinish = async(value: any, records: any) => {
    if (!value.comment) return
    let obj = {}
    if (value.comment == value1.value) {
      obj = {
        pcid: records.pcid,
        product_id: props.record.Product_Id,
        comment_id: value1.key,
        custom_comment: value.comment,
      }
    } else {
      obj = {
        pcid: records.pcid,
        product_id: props.record.Product_Id,
        comment_id: '',
        custom_comment: value.comment,
      }
    }
   
   await fetch(`${mainConfig.MAIN_ENDPOINT}/api/add-comment`, {
      method: 'post',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(obj),
    })
      .then(function (response) {
        if (response.status != 200) {
        }
        response.json().then(function (data) {
          if (data.success == true) {
            message.success(data.data)

            getSupplierData()
          }
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
      })
  }

  const columns = [
    {
      title: 'Supplier Code',
      dataIndex: 'supp_code',
      key: 'supp_code',
      className: 'table-color-1',
      sorter: (a: any, b: any) => a.supp_code.localeCompare(b.supp_code),
    },
    {
      title: 'Product Code',
      dataIndex: 'product_code',
      key: 'product_code',
      sorter: (a: any, b: any) => a.product_code.localeCompare(b.product_code),
    },

    {
      title: 'Forecast',
      dataIndex: 'forecast',
      key: 'forecast',
      editable: true,
      sorter: (a: any, b: any) => a.forecast.localeCompare(b.forecast),
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return (
          <>
            <div className='form-control'> {Helper.addZero(item)}</div>
          </>
        )
      },
    },
    {
      title: 'Price',
      dataIndex: 'price',
      key: 'price',
      editable: true,
      width: '10%',
      className: '',
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return (
          <>
            <div className='form-control'> {Helper.addZero(item)}</div>
          </>
        )
      },
      sorter: (a: any, b: any) => a.price.localeCompare(b.price),
      multiple: 1,
    },
    {
      title: 'Supplier Comments',
      dataIndex: 'comments',
      key: 'comments',
      editable: true,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return  <>
        <div className='form-control'> {item}</div>
      </>
      },

      sorter: (a: any, b: any) => a.price.localeCompare(b.price),
      multiple: 1,
    },
    {
      title: 'Date',
      dataIndex: 'date',
      key: 'date',
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return moment(item).format('DD-MMMM-YYYY')
      },

      sorter: (a: any, b: any) => a.date.localeCompare(b.date),
    },
    {
      title: 'Buyer Comments',
      key: 'buyer_comments',
      dataIndex: 'buyer_comments',
      width: '100',

      render: (tags: any, record: any) => {
        return <>{tags}</>
      },
    },
    {
      title: 'Add Comment',
      dataIndex: 'ProductAC4',
      key: 'ProductAC4',
      width: 200,

      render: (text: any, record: any, item: any) => {
        return (
          <Form onFinish={(e: any) => onCommentFinish(e, record)}>
            <Row gutter={16}>
              <Col className='gutter-row' span={18}>
                <div>
                  <Form.Item
                    name='comment'
                    rules={[{required: false, message: 'Please input your username!'}]}
                  >
                    <AutoComplete
                      className='editable-cell-value-wrap form-control mr-1'
                      style={{
                        paddingRight: 0,
                        height: 24,
                        width: 120,
                      }}
                      onSelect={(e: any, record: any) => {
                        setValue1(record)
                      }}
                      onSearch={(text: any) => {
                        getPanelValue(text)
                      }}
                      placeholder='Add comment'
                      ref={inputRef5}
                    >
                      {options.map((record: any) => (
                        <Option key={record.key} value={record.value}>
                          {record.value}
                        </Option>
                      ))}
                    </AutoComplete>
                  </Form.Item>
                </div>
              </Col>
              <Col className='gutter-row' span={6}>
                <div>
                  <Form.Item>
                    <Button
                      type='primary'
                      htmlType='submit'
                      icon={<PlusOutlined />}
                      size={'small'}
                    />
                  </Form.Item>
                </div>
              </Col>
            </Row>
          </Form>
        )
      },
    },
  ]
  const onNameChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setName(event.target.value)
  }
  const addItem = (e: React.MouseEvent<HTMLButtonElement | HTMLAnchorElement>) => {
    e.preventDefault()

    tag.push({value: name, label: name})
    setName('')
    setTimeout(() => {
      inputRef2.current?.focus()
    }, 0)
  }
  const components = {
    body: {
      row: EditableRow,
      cell: EditableCell,
    },
  }
  const columns1 = columns.map((col) => {
    if (!col.editable) {
      return col
    }

    return {
      ...col,
      onCell: (record: any) => ({
        record,
        editable: col.editable,
        dataIndex: col.dataIndex,
        title: col.title,
        handleSave: (row: any) => {
          handleSave({...record, oldPrice: record.price, ...row, oldComments: record.comments,oldForecast:record.forecast})
        },
      }),
    }
  })

  return (
    <>
      <Table
        components={components}
        columns={columns1}
        dataSource={dataSet}
        pagination={false}
        rowClassName={() => 'editable-row'}
        bordered
        loading={loader}
      />

      <Modal
        title='Attach Tag'
        open={openModal}
        onCancel={handleCancel}
        footer={null}
        destroyOnClose={true}
      >
        <Form
          form={form}
          name='control-hooks'
          onFinish={onFinish}
          style={{
            maxWidth: 600,
          }}
          preserve={false}
        >
          <div className='m-4'>
            <Form.Item
              name='tag'
              label='Tag'
              rules={[
                {
                  required: true,
                },
              ]}
              labelCol={{
                span: 4,
              }}
              wrapperCol={{
                span: 16,
              }}
            >
              <Select
                style={{
                  width: 300,
                }}
                placeholder='Attach tag'
                dropdownRender={(menu) => (
                  <>
                    {menu}
                    <Divider
                      style={{
                        margin: '8px 0',
                      }}
                    />
                    <Space
                      style={{
                        padding: '0 8px 4px',
                      }}
                    >
                      <Input
                        placeholder='Please enter new tag'
                        ref={inputRef2}
                        value={name}
                        onChange={onNameChange}
                        onKeyDown={(e) => e.stopPropagation()}
                      />
                      <Button type='text' icon={<PlusOutlined />} onClick={addItem}>
                        Add Tag
                      </Button>
                    </Space>
                  </>
                )}
                options={tag.map((item) => ({
                  label: item.label,
                  value: item.value,
                }))}
                allowClear
                showSearch
              />
            </Form.Item>
            <Form.Item
              name='severity'
              label='Severity'
              rules={[
                {
                  required: false,
                },
              ]}
              labelCol={{
                span: 4,
              }}
              wrapperCol={{
                span: 16,
              }}
            >
              <Select options={tagSeverity} showSearch={true} />
            </Form.Item>
          </div>
          <Form.Item>
            <Button type='primary' htmlType='submit'>
              Submit
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </>
  )
}

export default ExpandedRowRender
